import { Component } from "react"

/*

Example:

<Toggle>
  {({ on, toggle }) => (
    <div>
      {on && <div>On</div>}
      <button onClick={toggle}>{on ? 'close' : 'open'}</button>
    </div>
  )}
</Toggle>

*/

export default class Toggle extends Component {
  state = {
    on: false,
  }

  toggle = () => {
    this.setState({
      on: !this.state.on,
    })
  }

  render() {
    const { children } = this.props
    return children({ on: this.state.on, toggle: this.toggle })
  }
}
